import React, {useState} from 'react'
import {Valor} from './components/Valor'
import './App.css';

export function App() {

  const [uf, setUf] = useState("Cargando...")
  const [dolarO, setDolarO] = useState("Cargando...")
  const [dolarA, setDolarA] = useState("Cargando...")
  const [euro, setEuro] = useState("Cargando...")
  const [ipc, setIPC] = useState("Cargando...")
  const [utm, setUTM] = useState("Cargando...")
  const [ivp, setIVP] = useState("Cargando...")
  const [imacec, setImacec] = useState("Cargando...")

  const message = `<> with ♥ by Bastián Escribano`
  const why = `{Hooks}`
  fetch('https://mindicador.cl/api/')
  .then(Response => {
    Response.json().then(data =>{
      setUf(data.uf.valor)
      setDolarO(data.dolar.valor)
      setDolarA(data.dolar_intercambio.valor) 
      setEuro(data.euro.valor)
      setIPC(data.ipc.valor)
      setUTM(data.utm.valor)
      setIVP(data.ivp.valor)
      setImacec(data.imacec.valor)})})

  return (<>
      
      <h2>Money Values API Fetch</h2>
      <h3>Using React {why} useState and API Fetch</h3>
      
      <el className="todo">
 <r>
      <Valor Nombre={"Valor de la U.F"} Valor={uf}/>
      <Valor Nombre={"Valor del Dólar Observado"} Valor={dolarO}/>
      </r>

      <r>
      <Valor Nombre={"Valor del Dólar Acuerdo"} Valor={dolarA}/>
      <Valor Nombre={"Valor del Euro"} Valor={euro}/>
      </r>

      <r>
      <Valor Nombre={"Valor del IPC"} Valor={ipc}/>
      <Valor Nombre={"Valor de la UTM"} Valor={utm}/>
      </r>

      <r>
      <Valor Nombre={"Valor del IVP"} Valor={ivp}/>
      <Valor Nombre={"Valor del Imacec"} Valor={imacec}/>
      </r>
      </el>
      
     
    
      <footer>{message}</footer>

  
    </>)}
